const { ccclass, property } = cc._decorator;
import * as dd from './../../Modules/ModuleManager';
@ccclass
export default class LoadCanvas extends cc.Component {

    /**
     * 
     * 
     * @memberof LoadCanvas
     */
    @property({ url: cc.RawAsset })
    manifestUrl = null;
    /**
     * 
     * 
     * @type {cc.Label}
     * @memberof LoadCanvas
     */
    @property(cc.Label)
    lbl_err: cc.Label = null;
    /**
     * 
     * 
     * @type {boolean}
     * @memberof LoadCanvas
     */
    _needUpdate: boolean = false;
    /**
     * 
     * 
     * @type {any}
     * @memberof LoadCanvas
     */
    _am: any = null;//
    /**
     * 
     * 
     * @type {any}
     * @memberof LoadCanvas
     */
    _checkListener: any = null;
    /**
     * 
     * 
     * @type {any}
     * @memberof LoadCanvas
     */
    _updateListener: any = null;
    /**
     * 
     * 
     * @type {number}
     * @memberof LoadCanvas
     */
    _failCount: number = 0;
    /**
     * 
     * 
     * @memberof LoadCanvas
     */
    download: (data: any) => void = (json: any) => {
        switch (cc.sys.os) {
            case cc.sys.OS_ANDROID:
                dd.js_call_native.openBrowser(json.apkUrl);
                break;
            case cc.sys.OS_IOS:
                dd.js_call_native.openBrowser(json.ipaUrl);
                break;
            default:
                dd.js_call_native.openBrowser(json.apkUrl);
                break;
        }
    }
    /**
     * 
     * 
     * @memberof LoadCanvas
     */
    error: (msg: string) => void = (msg: string) => {
        dd.ui_manager.hideLoading();
        this.lbl_err.string = msg;
    }

    async onLoad() {
        // //
        (<any>window).dd = dd;
        dd.config.wxState = dd.js_call_native.initWX(dd.config.app_id, dd.config.secret);
        dd.init();
        //
        try {
            await dd.ui_manager.initUI();
            dd.ui_manager.showLoading(',');
            await dd.img_manager.initIMG();
            await dd.mp_manager.initMP();
        }
        catch (errMsg) {
            this.error(errMsg + ',!');
            return;
        }
        await this.checkAppVersion();
    }

    /**
     * appweb
     * 
     * @memberof LoadCanvas
     */
    async checkAppVersion(): Promise<void> {
        let ver = dd.js_call_native.getAppVersion();
        if (ver.length > 0) {
            let response: Response = await fetch(dd.config.checkUrl + ver);
            if (response.ok) {
                let json: checkData = await response.json();
                dd.config.cd = json;
                if (json.type < 0) {
                    dd.ui_manager.showAlert(2, ',!', () => {
                        this.download(json);
                    });
                } else if (json.type === 1) {
                    dd.ui_manager.showAlert(1, ',?', () => {
                        this.download(json);
                    }, () => {
                        this.checkResVersion();
                    });
                } else {
                    this.checkResVersion();
                }
            } else {
                this.error('APP!');
            }
        } else {
            this.jumpScene();
        }
    }

    /**
     * web
     * 
     * @memberof LoadCanvas
     */
    checkResVersion(): void {
        let storagePath = ((jsb.fileUtils ? jsb.fileUtils.getWritablePath() : '/') + 'platform-remote-asset');
        this._am = new jsb.AssetsManager(this.manifestUrl, storagePath);
        this._am.retain();
        if (this._am.getLocalManifest().isLoaded()) {
            this._checkListener = new jsb.EventListenerAssetsManager(this._am, this.checkCb.bind(this));
            cc.eventManager.addListener(this._checkListener, 1);
            this._am.checkUpdate();
        } else {
            this.error('!');
        }
    }

    /**
     * 
     * 
     * @param {any} event 
     * @memberof LoadCanvas
     */
    checkCb(event) {
        switch (event.getEventCode()) {
            case jsb.EventAssetsManager.ERROR_NO_LOCAL_MANIFEST:
                this.error('');
                cc.eventManager.removeListener(this._checkListener);
                break;
            case jsb.EventAssetsManager.ERROR_DOWNLOAD_MANIFEST:
            case jsb.EventAssetsManager.ERROR_PARSE_MANIFEST:
                dd.ui_manager.showAlert(2, '', () => {
                    cc.game.end();
                })
                cc.eventManager.removeListener(this._checkListener);
                break;
            case jsb.EventAssetsManager.ALREADY_UP_TO_DATE:
                cc.log("");
                cc.eventManager.removeListener(this._checkListener);
                this.jumpScene();
                break;
            case jsb.EventAssetsManager.NEW_VERSION_FOUND:
                dd.ui_manager.showLoading('');
                this._needUpdate = true;
                cc.eventManager.removeListener(this._checkListener);
                break;
            default:
                break;
        }
    }

    update(dt) {
        if (this._am && this._needUpdate) {
            this._needUpdate = false;
            this._updateListener = new jsb.EventListenerAssetsManager(this._am, this.updateCb.bind(this));
            cc.eventManager.addListener(this._updateListener, 1);
            this._failCount = 0;
            this._am.update();
        }
    }

    updateCb(event) {
        let needRestart = false;
        let failed = false;
        switch (event.getEventCode()) {
            case jsb.EventAssetsManager.ERROR_NO_LOCAL_MANIFEST:
                cc.log("");
                failed = true;
                break;
            case jsb.EventAssetsManager.UPDATE_PROGRESSION:
                dd.ui_manager.showLoading('(' + (event.getPercentByFile() * 100).toFixed(2) + '%)');
                break;
            case jsb.EventAssetsManager.ERROR_DOWNLOAD_MANIFEST:
            case jsb.EventAssetsManager.ERROR_PARSE_MANIFEST:
                cc.log("");
                failed = true;
                break;
            case jsb.EventAssetsManager.ALREADY_UP_TO_DATE:
                cc.log("");
                failed = true;
                break;
            case jsb.EventAssetsManager.UPDATE_FINISHED:
                cc.log('' + event.getMessage());
                needRestart = true;
                break;
            case jsb.EventAssetsManager.UPDATE_FAILED:
                cc.log('' + event.getMessage());
                this._failCount++;
                if (this._failCount < 5) {
                    this._am.downloadFailedAssets();
                }
                else {
                    cc.log('');
                    this._failCount = 0;
                    failed = true;
                }
                break;
            case jsb.EventAssetsManager.ERROR_UPDATING:
                cc.log('Asset update error: ' + event.getAssetId() + ', ' + event.getMessage());
                break;
            case jsb.EventAssetsManager.ERROR_DECOMPRESS:
                cc.log(event.getMessage());
                break;
            default:
                break;
        };

        if (failed) {
            //
            cc.eventManager.removeListener(this._updateListener);
            dd.ui_manager.showAlert(2, '', () => {
                cc.game.end();
            })
        }
        if (needRestart) {
            cc.eventManager.removeListener(this._updateListener);
            let searchPaths = jsb.fileUtils.getSearchPaths();
            let newPaths = this._am.getLocalManifest().getSearchPaths();
            Array.prototype.unshift(searchPaths, newPaths);
            cc.sys.localStorage.setItem('HotUpdateSearchPaths', JSON.stringify(searchPaths));
            jsb.fileUtils.setSearchPaths(searchPaths);
            dd.ui_manager.showLoading('');
            setTimeout(() => {
                dd.destroy();
                cc.sys.garbageCollect();
                cc.game.restart();
            }, 1000);
        }
    }

    /**
     * 
     * 
     * @memberof LoadCanvas
     */
    jumpScene(): void {
        setTimeout(() => {
            cc.director.loadScene('LoginScene');
        }, 1000);
    }

    onDestroy(): void {
        this._am && this._am.release();
    }
}
