const { ccclass, property } = cc._decorator;
import * as dd from './../../Modules/ModuleManager';

@ccclass
export default class EmailItem extends cc.Component {
    /**
     * 
     * 
     * @type {cc.Sprite}
     * @memberof EmailItem
     */
    @property(cc.Sprite)
    headImg: cc.Sprite = null;
    /**
     * 
     * 
     * @type {cc.RichText}
     * @memberof EmailItem
     */
    @property(cc.RichText)
    msg: cc.RichText = null;
    /**
     * 
     * 
     * @type {string}
     * @memberof EmailItem
     */
    modelMsg: string = ':<color=#D3AE6C><b><></b></c>,<br/>:<color=#D3AE6C><b><></b></c>,<br/>?';
    /**
     * 
     * 
     * @type {DzpkerOrderItem}
     * @memberof EmailItem
     */
    data: DzpkerOrderItem = null;
    /**
     * 
     * 
     * @memberof EmailItem
     */
    callback: (bt: number, id: string, node: cc.Node) => void = null;//1,2

    async init(data: DzpkerOrderItem, callback: (bt: number, id: string, node: cc.Node) => void) {
        this.data = data;
        this.callback = callback;
        this.headImg.spriteFrame = await dd.img_manager.loadURLImage(data.headImg);
        this.msg.string = this.modelMsg.replace('<>', dd.utils.getStringBySize(data.nick, 12)).replace('<>', data.chipNum);
    }
    /**
     * 
     * 
     * @memberof EmailItem
     */
    click_yes() {
        this.callback(1, this.data.itemId, this.node);
    }
    /**
     * 
     * 
     * @memberof EmailItem
     */
    click_no() {
        this.callback(2, this.data.itemId, this.node);
    }

}
